.about_me_wrapper{
    .about_me_inner{
        .app_title{
            font-size: 18px;
            text-transform: uppercase;
            color: #787878;
        }
      .title_line{
          border: 1px solid #FE744F;
          width: 80px;
          margin-left: 20px;
          display: inline-block;
          text-align: center;
      }
      .btn_download{
          background: transparent;
          border: 1px solid #FE744F;
          font-weight: normal;
          margin: auto;
          padding: 10px 20px 10px 20px;
          &:hover{
              background: #FE744F;
              color:black;
          }
      }
    }


    .about_me_img_inner{
        .about_me_bg_layer{
            z-index: 10;
            top: 0;
            left: 0;
            img{
                width: 80%;
            }
        }
    }

    .pf_descp_wrapper{
        width: 100%;
        display: flex;
        justify-content: space-evenly;
