.skills{
    flex-wrap: wrap;
    p{
        padding: 5px 20px 5px 20px;
        border: 1px solid #eee;
        font-size: 12px;
        color: black;
        border-radius: 3px;
        margin-right: 5px;
        background: rgb(245,245,245);
        &:hover{
            background: #eee;
            cursor: pointer;
        }
    }

}

